import React from "react"
import { Link } from "gatsby"
import Layout from "../components/Layout"
import appsData from '../data/apps.json'

const IndexPage = () => {
  return (
    <Layout>
      <h1>App Gallery</h1>
      <p>Total categories: {appsData.meta.totalCategories}</p>
      <p>Total apps: {appsData.meta.totalApps}</p>
      
      {Object.entries(appsData.categories).map(([categoryName, apps]) => (
        <div key={categoryName}>
          <h2>
            <Link to={`/category/${categoryName.toLowerCase()}`}>
              {categoryName} ({apps.length} apps)
            </Link>
          </h2>
        </div>
      ))}
    </Layout>
  )
}

export default IndexPage